<template>
    <div class="signin">
        <header>
            <router-link class="signin-rule" to="/noteSignIn">签到规则</router-link>
            <div class="accumulate-points"><span class="points">{{totalData.integral}}</span></div>
            <p class="my-point-text">我的积分</p>
        </header>
        <div class="signin-box">
            <ul class="week-box">
                <li class="daily" v-for="(item,index) in signArr" :key="index">
                    <div class="daily-num"
                         :class="{
                             'signin-yes':item.sign===1,
                             'signin-no':item.sign===0
                         }"
                         @click="signNow(1,$event,index)">{{index+1}}
                    </div>
                    <div class="got-point">{{item.getPoint}}</div>
                </li>
            </ul>
            <van-button class="signin-btn" round type="danger"
                        @click="signNow(0)"
                        :class="{'gray-btn':todaySignIn}">签 到
            </van-button>
        </div>
        <!--<ul class="prize-draw">-->
        <!--<li class="draw-item" v-for="(item,index) in 3" :key="index">-->
        <!--<img class="draw-logo" src="../../assets/images/haha.jpg" alt="">-->
        <!--<div class="draw-intro">-->
        <!--<h3 class="draw-name">自撒旦法</h3>-->
        <!--<p class="draw-description">阿斯顿发斯蒂芬阿斯顿发放</p>-->
        <!--</div>-->
        <!--<div class="join-draw-btn" @click="$isApp">立即参与</div>-->
        <!--</li>-->
        <!--</ul>-->
        <sign-popup
                :title="popData.title"
                :description="popData.description"
                :picUrl="popData.picUrl"
                :buttonText="popData.buttonText"
                :showPopup="showPopup"
                v-on:close-popup="closePop($event)"
                v-on:only-close-popup="onlyClosePop"
        ></sign-popup>
    </div>
</template>

<script>
  import signPopup from './children/SignPopup'

  export default {
    name: "signIn",
    components: {
      signPopup
    },
    data() {
      return {
        totalData: [],// 所有数据
        dataString: [],// 后台返回的签到的集合
        signArr: [],// 渲染七天数据
        signType: 0,// 签到类型 0签到 1补签
        isVip: 0,
        todaySignIn: false,// 今天是否签到
        showPopup: false,
        todayNum: new Date().getDay() === 0 ? 7 : new Date().getDay(),
        popData: {
          title: '补签提醒',
          description: '开启会员，独享补签特权',
          picUrl: require('../../assets/images/buqian.png'),
          buttonText: '立即成为会员'
        }
      }
    },
    mounted() {
      let params = this.$queryParams(location.href);
      this.getData(params);
      this.getVipStatus();


    },
    created() {
      document.title = '签到';
    },
    methods: {
      showAlert: function () {
        if (this.isVip === 1) {
          this.popData = {
            title: '补签提醒',
            description: '开启会员，独享补签特权',
            picUrl: require('../../assets/images/buqian.png'),
            buttonText: '我知道了'
          }
        } else {
          this.popData = {
            title: '补签提醒',
            description: '开启会员，独享补签特权',
            picUrl: require('../../assets/images/buqian.png'),
            buttonText: '立即成为会员'
          }
        }
      },
      // 获取用户积分及签到列表
      getData: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE1 + '/userIntegral/getWeekSignIn',//获取签到列表
          data: this.$qs.stringify({
            userId: params.userId,
            token: params.token
          })
        }).then((res) => {
            if (res !== null) {
              if (res.data.status === 1000) {
                this.totalData = res.data.data;
                if (this.totalData.userSigninList.length === this.todayNum) {
                  this.todaySignIn = true;
                }
                // 把签到的数字存起来
                for (let i = 0; i < this.totalData.userSigninList.length; i ++) {
                  let signNum = new Date(this.totalData.userSigninList[i].createtime).getDay();
                  this.dataString.push(signNum === 0 ? 7 : signNum);
                  this.dataString.sort();
                }
                for (let i = 1; i <= 7; i ++) {
                  if (this.dataString.indexOf(i) > - 1) {
                    this.signArr.push({
                      getPoint: '+5',
                      sign: 1
                    });
                  } else if (i >= this.todayNum) {
                    this.signArr.push({
                      getPoint: '+5',
                      sign: 2
                    });
                  } else {
                    this.signArr.push({
                      getPoint: '补签',
                      sign: 0
                    });
                  }
                }
                // 如果有漏签的 提醒补签
                if (this.dataString.length > 0) {
                  if (this.dataString.length < this.dataString[this.dataString.length - 1]) {
                    // this.showPopup = true;
                    // this.showAlert();
                  }
                } else {
                  if (this.todayNum !== 1) {

                  }
                }
              }
            } else {
              this.$toast(res.data.msg);
            }
          }
        ).catch((err) => {
          console.warn(err);
        })
      },
      // 签到
      signNow: function (signType, e, signIndex) {
        let params = this.$queryParams(location.href);
        let nowTime = 0;

        if (signType === 1) {// 点的是补签按钮
          let className = e.target._prevClass;
          if (className.indexOf('signin-no') === - 1) {
            return false;
          }
          if (this.isVip) {
            nowTime = new Date().getTime() - (this.todayNum - signIndex - 1) * 86400000;
          } else {
            // this.$toast('您尚未开通会员');
            this.showPopup = true;
            this.showAlert();
            return false;
          }
        }
        if (signType === 0) {// 点的签到
          if (this.todaySignIn) {
            this.$toast('今日已签到');
            return false;
          }
          nowTime = new Date().getTime();
        }
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE1 + '/userIntegral/userSignIn',//签到
          data: this.$qs.stringify({
            userid: params.userId,
            userId: params.userId,
            signintype: signType,
            signintime: nowTime,
            token: params.token
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.showPopup = true;
              if (signIndex !== undefined) {
                this.dataString.push(signIndex + 1);
                this.dataString.sort();
                this.signArr.splice(signIndex * 1, 1, {
                  getPoint: '+5',
                  sign: 1
                });
              } else {
                // 把今天放到签到的最后
                this.dataString.push(this.todayNum);
                this.todaySignIn = true;
                this.dataString.sort();
                this.signArr.splice(this.todayNum - 1, 1, {
                  getPoint: '+5',
                  sign: 1
                });
              }
              if (this.dataString.length <= 2) {
                this.totalData.integral += 5;
              }
              if (this.dataString.length === 3) {
                this.totalData.integral += 15;
              }
              if (this.dataString.length === 4) {
                this.totalData.integral += 5;
              }
              if (this.dataString.length === 5) {
                this.totalData.integral += 25;
              }
              if (this.dataString.length === 6) {
                this.totalData.integral += 5;
              }
              if (this.dataString.length === 7) {
                this.totalData.integral += 35;
              }
              this.popData = {
                title: signIndex !== undefined ? '补签成功' : '签到成功',
                description: [this.dataString.length, this.totalData.daySigninIntegral],
                picUrl: require('../../assets/images/qiandaochenggong.png'),
                buttonText: '我知道了'
              };
            } else if (res.data.status === 1001) {
              this.$toast('今日已签到');
            } else {
              this.$toast(res.data.msg);
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      closePop: function (e) {
        if (e.indexOf('立即成为会员') > - 1) {
          this.showPopup = false;
          // this.$router.push('/isApp');
          window.location.href = 'sweetfans://signIn'
        }
        if (e.indexOf('我知道了') > - 1) {
          this.showPopup = false;
        }
      },
      onlyClosePop: function () {
        this.showPopup = false;
      },
      getVipStatus: function (e) {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE1 + '/user_server/tu-user/getUserById',//获取用户vip状态
          data: this.$qs.stringify({
            userId: params.userId,
            token: params.token
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.isVip = res.data.data.vipStatus;
            } else {
              this.$toast(res.data.msg);
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
    .gray-btn {
        background: #75807B !important;
        border: none !important;
        color: #fff;
    }

    .sign-popup {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
    }

    .signin-yes {
        background: #FF2E4D !important;
    }

    .signin-no {
        background: #EAEAEA !important;
    }

    .signin {
        width: 100%;
        min-height: 100vh;
        background: #fff;
        header {
            height: 4.19rem;
            width: 100%;
            background: url("../../assets/images/qiandaobeijing.png") center no-repeat;
            background-size: cover;
            position: relative;
            overflow: hidden;
            .signin-rule {
                width: 1.4rem;
                height: .5rem;
                line-height: .5rem;
                text-align: center;
                border: 1px solid #fff;
                color: #fff;
                font-size: .28rem;
                -webkit-border-radius: .5rem;
                -moz-border-radius: .5rem;
                border-radius: .5rem;
                position: absolute;
                right: .5rem;
                bottom: 3rem;
            }
            .accumulate-points {
                width: 100%;
                font-size: .7rem;
                color: #fff;
                text-align: center;
                margin: 2rem 0 .2rem 0;
                overflow: hidden;
                .points {
                    &:after {
                        display: inline-block;
                        content: '';
                        height: .26rem;
                        width: .26rem;
                        background: url("../../assets/images/jifen.png") center no-repeat;
                        background-size: cover;
                    }
                }

            }
            .my-point-text {
                width: 100%;
                font-size: .3rem;
                color: #fff;
                text-align: center;
            }
        }
        .signin-box {
            width: 7.02rem;
            height: 3.14rem;
            background: #fff;
            box-shadow: 0 .03rem .14rem 0 rgba(234, 56, 243, 0.24);
            position: absolute;
            left: 50%;
            top: 3.5rem;
            margin-left: -3.51rem;
            -webkit-border-radius: .1rem;
            -moz-border-radius: .1rem;
            border-radius: .1rem;
            .week-box {
                width: 100%;
                height: 1rem;
                display: flex;
                margin: .48rem 0 0 0;
                justify-content: space-around;
                .daily {
                    height: 100%;
                    width: 100%;
                    .daily-num {
                        width: .5rem;
                        height: .5rem;
                        line-height: .5rem;
                        margin: 0 auto;
                        color: #fff;
                        background: #FEE7F1;
                        text-align: center;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        border-radius: 50%;
                    }
                    .got-point {
                        width: 100%;
                        text-align: center;
                        color: #909090;
                        font-size: .24rem;
                        overflow: hidden;
                        margin: .22rem 0;
                    }
                }
            }
        }
        .signin-btn {
            display: block;
            width: 3.8rem;
            height: .76rem;
            background: #FF2E4D;
            text-align: center;
            line-height: .76rem;
            -webkit-border-radius: .38rem;
            -moz-border-radius: .38rem;
            border-radius: .38rem;
            margin: .5rem auto;
        }
        .prize-draw {
            width: 7.2rem;
            margin: 3rem auto 0;
            li.draw-item {
                height: 1.8rem;
                width: 100%;
                box-shadow: 0 .03rem .14rem 0 rgba(234, 56, 243, 0.24);
                display: flex;
                justify-content: space-around;
                .draw-logo {
                    width: 1.14rem;
                    height: 1.03rem;
                    margin: .385rem;
                }
                .draw-intro {
                    height: 1.03rem;
                    width: 3rem;
                    margin: .385rem .385rem .385rem 0;
                    .draw-name, .draw-description {
                        width: 100%;
                        height: 50%;
                        font-size: .34rem;
                        font-weight: bold;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                        line-height: .6rem;
                    }
                    .draw-description {
                        font-size: .28rem;
                        font-weight: normal;
                    }
                }
                .join-draw-btn {
                    width: 1.5rem;
                    height: .56rem;
                    line-height: .56rem;
                    font-size: .28rem;
                    color: #FF2E4D;
                    margin: .62rem .3rem .62rem 0;
                    text-align: center;
                    border: .02rem solid #FF2E4D;
                    -webkit-border-radius: .3rem;
                    -moz-border-radius: .3rem;
                    border-radius: .3rem;
                }
            }
        }
    }
</style>
