<template>
    <transition name="fade">
        <div class="sign-popup-box" v-show="showPopup">
            <div class="mask" @click="onlyClose"></div>
            <div class="signPopup">
                <h3 class="pop-title">{{title}}</h3>
                <p class="pop-description" v-if="this.title.indexOf('补签提醒') > -1">{{description}}</p>
                <p class="pop-description" v-else>累计签到 <span
                        class="mark-color">{{description[0]}}天</span>，今日获得<span
                        class="mark-color">{{description[1]}}积分</span></p>
                <img class="pop-pic" :src="picUrl"/>
                <button class="pop-btn" @click="close">{{buttonText}}</button>
            </div>
        </div>
    </transition>
</template>

<script>
  export default {
    name: "signPopup",
    props: {
      // 基础的类型检查 (`null` 和 `undefined` 会通过任何类型验证)
      title: {
        type: String,
        required: true
      },
      // 多个可能的类型
      description: {
        type: [String, Array],
        required: true
      },
      picUrl: {
        type: String,
        required: true
      },
      showPopup: {
        type: [Boolean, String],
        required: true
      },
      buttonText: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        show: this.showPopup
      }
    },
    methods: {
      close: function () {
        this.$emit('close-popup', this.buttonText);
      },
      onlyClose: function () {
        this.$emit('only-close-popup', false);
      }
    }
  }
</script>

<style scoped lang="scss">
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
    }

    .mark-color {
        color: #FF2E4D;
    }

    .mask {
        width: 100vw;
        height: 100vh;
        position: fixed;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, .7);
        z-index: 5;
    }

    .signPopup {
        width: 5.86rem;
        height: 6.48rem;
        background: rgba(255, 255, 255, 1);
        border-radius: .1rem;
        padding: .3rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        position: fixed;
        top: 50%;
        left: 50%;
        max-height: 100%;
        overflow-y: auto;
        -webkit-transition: .3s ease-out;
        transition: .3s ease-out;
        -webkit-overflow-scrolling: touch;
        -webkit-transform: translate3d(-50%, -50%, 0);
        transform: translate3d(-50%, -50%, 0);
        z-index: 6;
        .pop-title {
            text-align: center;
            color: #333333;
            font-size: .42rem;
            width: 100%;
            font-weight: bold;
        }
        .pop-pic {
            height: 2.1rem;
            width: 2.1rem;
        }
        .pop-btn {
            width: 3.8rem;
            line-height: .76rem;
            height: .76rem;
            background: #FF2E4D;;
            text-align: center;
            color: #fff;
            -webkit-border-radius: .5rem;
            -moz-border-radius: .5rem;
            border-radius: .5rem;
        }
    }
</style>
